export class TaskCommentComponent implements OnInit, ControlValueAccessor {

  @Input() showAttachment: boolean = true;//æ¯å¦æ¾ç¤ºéä»¶ä¸ä¼ ï¼ç»ä»¶æ§å¶
  @Input() showSign: boolean = true;//æ¯å¦æ¾ç¤ºç­¾åï¼ç»ä»¶æ§å¶ï¼ä¸åå¨ä½å¯è½ä¼ä¸æ¾ç¤ºï¼
  @Input() showCommonComment = true;//æ¯å¦æ¾ç¤ºå¸¸ç¨æè§
  @Input() comment: string;
  @Input() placeholder = '';
  @Input() processInstanceId: string;
  @Input() textAreaHeight: number = 120;//æå­æ¡çé«åº¦

  @Input()
  set TaskCenterConfig(configList: TaskCenterConfig[]) {
    const attachment = configList.find(a => a.configCode == ConfigCode.If_Show_Attachment);
    const sign = configList.find(a => a.configCode == ConfigCode.If_Show_Signature);
    const enableSign = configList.find(a => a.configCode == ConfigCode.If_Use_Sign_By_Default);
    const editSign = configList.find(a => a.configCode === ConfigCode.If_Can_Edit_Signature);
    if ((window as any).gspframeworkService.common) {
      this.curUserId = (window as any).gspframeworkService.common.userInfos.get().userId;
    } else {
      this.curUserId = (window as any).parent.gspframeworkService.common.userInfos.get().userId;
    }
    attachment && attachment.currentValue === 'true' && (this.ifShowAttachment = true);
    sign && sign.currentValue === 'true' && (this.ifShowSign = true);
    enableSign && enableSign.currentValue === 'false' && (this.ifEnableSign = false);
    editSign && editSign.currentValue === 'false' && (this.ifCanEditSign = false);
    if (this.ifShowSign && this.ifEnableSign) {
      this.getUserSign(this.curUserId);
    }
  }

  @Output() attachment = new EventEmitter();//éä»¶
  @Output() signature = new EventEmitter();//ä¸ä¼ ç­¾åä¹åï¼åéç­¾åid
  @ViewChild('commentDialog') commentDialog: DialogComponent;//å¸¸ç¨æè§ç®¡ç
  @ViewChild('signDialog') signDialog: DialogComponent;//æåç­¾åå¼¹æ¡
  @ViewChild('dropdownEl') dropDownEl: FDropdownDirective;//è§£å³éæ©æè§åï¼ä¸æèåæ æ³æ¶æçé®é¢
  @ViewChild('wfSignHere') signaturePad: SignHereComponent;//æåç­¾åç»ä»¶
  @ViewChild('farrisAvatar') farrisAvatar: AvatarComponent;//å¤´åç»ä»¶ï¼ç¨ä½ç­¾åå¾çä¸ä¼ 
  @ViewChild('dataGrid') dataGrid: DatagridComponent;
  @ViewChild('commentTypeCell') commentTypeCell: TemplateRef<any>;//å¸¸ç¨æè§ç±»å«æ¨¡æ¿
  @ViewChild('commentOperateCell') commentOperateCell: TemplateRef<any>;//å¸¸ç¨æè§å é¤

  private _value = '';
  attachmentList: any[] = [];
  ifShowAttachment: boolean = false;//ä»»å¡ä¸­å¿éç½®ï¼æ¯å¦æ¾ç¤ºéä»¶ä¸ä¼ 
  ifShowSign: boolean = false;//ä»»å¡ä¸­å¿éç½®ï¼æ¯å¦æ¾ç¤ºæåç­¾å
  curUserId: string;//å½åç¨æ·id
  signatureImage: string;//ç­¾åå¾ççbase64å¼
  tmpSignatureImage: string;//ä¸´æ¶ä¿å­ä¸ä¼ çå¾ç
  downloadService = new DownloadService();
  ifUseSign: boolean = false;//æ¯å¦ä½¿ç¨æå
  ifUseImg: boolean = true;//æ¯å¦ä½¿ç¨ä¸ä¼ å¾ç
  ifEnableSign: boolean = true;//æ¯å¦é»è®¤å¯ç¨ç­¾å
  ifUploadImg: boolean = false;//æ¯å¦ä¸ä¼ è¿å¾ç
  ifCanEditSign: boolean = true;//æ¯å¦åè®¸ç¼è¾ç­¾å
  originSignId: string;//åå§çç­¾åid

  commentList: CommonComment[] = [];
  originCommentList: CommonComment[] = [];

  commentColumns: DataColumn[] = [];

  set value(v) {
    if (this._value !== v) {
      this._value = v;
      this.onModelChange(v);
    }

  }
  get value() {
    return this._value;
  }

  onModelChange = (obj: any) => { };
  onModelTouched = (obj: any) => { };

  constructor(
    private taskService: TaskService,
    private httpSvc: HttpService,
    private uploadDialog: UploadDialogService,
    private messageService: MessagerService,
    private i18nService: I18nService,
    private loadingService: LoadingService,
    private notify: NotifyService,
  ) {

  }

  ngOnInit() {
    this.commentColumns = [{
      field: 'message',
      width: 100,
      title: this.i18nService.getValue('comment.static.content'),
      editor: { type: EditorTypes.TEXTBOX, options: {} },
    }, {
      field: 'userId',
      width: 20,
      title: this.i18nService.getValue('comment.static.type'),
      template: this.commentTypeCell
    }, {
      field: 'operate',
      width: 20,
      title: this.i18nService.getValue('comment.static.operate'),
      template: this.commentOperateCell
    }];
    // è·åå¸¸ç¨æè§åè¡¨
    this.loadComments();
  }

  onEditComment() {
    this.dropDownEl.close();
    this.commentDialog.show();
  }

  loadComments() {
    this.taskService.getCommentList().subscribe((data: any[]) => {
      this.commentList = data;
      this.originCommentList = [...data];
    });
  }

  /**
   * ControlValueAccessorå¿é¡»å®ç°çæ¹æ³
   */
  writeValue(value: string) {
    if (value !== this.value) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.onModelChange = fn;
  }

  registerOnTouched(fn) {
    this.onModelTouched = fn;
  }

  /**
   * éæ©ä¸æ¡å¸¸ç¨æè§åäºä»¶
   */
  buttonClick(data: any) {
    this.dropDownEl.close();
    this.value = data;
  }

  /**
   * ä¸ä¼ éä»¶
   */
  uploadAttachment() {
    let isRepeat = false;
    this.uploadDialog.uploadFile(this.processInstanceId, 'wf_attachment').then(re => {
      for (let i of (re as any[])) {
        isRepeat = false;
        for (let attach of this.attachmentList) {
          if (attach.fileName === i.fileName) {
            this.messageService.info('åå«éå¤æä»¶' + i.fileName);
            isRepeat = true;
            break;
          }
        }
        if (!isRepeat) {
          this.attachmentList.push(i);
        }
      }
      this.attachment.emit(this.attachmentList);
      //æ§è¡ä¸ä¼ éä»¶
      let files = new FileUploadEntity();
      files.rootId = "wf_attachment";
      files.metadataIdList = [];
      this.attachmentList.forEach(a => {
        files.metadataIdList.push(a.metadataId);
      })
      files.metadataIdList.length && this.taskService.uploadAttachment(files).subscribe(() => { });
    });
  }

  /**
   * å é¤éä»¶
   */
  attachmentDelete(attachment) {
    var gspDocDeleteEntity: FileDeleteEntity = {
      rootId: "wf_attachment",
      metadataId: attachment.metadataId,
      mode: "Formal"
    }
    let index = this.attachmentList.indexOf(attachment);
    if (index > -1) {
      this.attachmentList.splice(index, 1);
    }
    this.attachment.emit(this.attachmentList);
    this.taskService.deleteAttachment(gspDocDeleteEntity).subscribe(() => { });
  }

  /**
   * è·åä¸åæ ¼å¼éä»¶çç¼©ç¥å¾
   */
  imageUrl(attachmentInfo) {
    const url = '/platform/runtime/wf/webapp/mobiletaskcenter/assets/accessory/';
    return url + this.taskService.createItem(attachmentInfo) + '.svg';
  }

  /**
   * å±ç¤ºæåç­¾ådialog
   */
  showSignDialog() {
    this.signDialog.show();
    if (!this.ifEnableSign) {
      this.getUserSign(this.curUserId);
    }
  }

  /**
 * ç¹å»å é¤ç­¾å
 */
  clickToDeleteImage() {
    this.signatureImage = '';
    this.signature.emit(null);
    const load = this.loadingService.show();
    this.taskService.addTaskCenterConfig(ConfigCode.If_Use_Sign_By_Default, 'false').subscribe(re => {
      load.close();
    }, () => {
      load.close();
    })
  }

  clickToUseSign() {
    this.ifUseImg = false;
    this.ifUseSign = true;
    const canvas: any = this.signaturePad.canvasDiv.nativeElement.querySelector('canvas');
    canvas.height = 252;
    canvas.width = 665;
  }

  clickToUploadImg() {
    this.farrisAvatar.onClick();
  }

  clickToConfirm() {
    let image: any;
    if (this.signaturePad && this.signaturePad.isDraw) {//å¦æç­¾è¿å
      image = this.signaturePad.confirm();
    } else if (this.ifUploadImg) {//å¦æä¸ä¼ è¿å¾ç
      image = this.tmpSignatureImage;
    }
    if (image) {
      this.uploadSignatureImg(image);
    } else {
      if (this.tmpSignatureImage) {
        this.signatureImage = this.tmpSignatureImage;
        this.signature.emit(this.originSignId);
        if (!this.ifEnableSign) {
          const load = this.loadingService.show();
          this.taskService.addTaskCenterConfig(ConfigCode.If_Use_Sign_By_Default, 'true').subscribe(re => {
            load.close();
          }, () => {
            load.close();
          })
        }
      }
      this.signDialog.close();
    }
  }

  clickToReset() {
    this.ifUseSign = false;
    this.ifUseImg = true;
    this.signatureImage = '';
    this.tmpSignatureImage = '';
    this.signature.emit('');
  }

  clickToCancel() {
    this.signDialog.close();
  }

  /**
   * ä¸ä¼ å¾çåäºä»¶
   * event.base64 æ¯å¾çä¿¡æ¯,event.nameæ¯å¸¦åç¼çå¾çåï¼event.typeå¦image/jpeg
   */
  imgChange(event: any) {
    this.ifUseImg = true;
    this.ifUseSign = false;
    this.ifUploadImg = true;
    const image = event.base64;
    this.tmpSignatureImage = image;
    this.signatureImage = image;
  }

  private uploadSignatureImg(image: any) {
    var uploadSignature = new GspDocUploadEntity();
    uploadSignature.fileContent = image.split(',')[1];
    uploadSignature.filePath = this.curUserId;
    uploadSignature.mode = "Formal";
    uploadSignature.metadata = new GspDocMetadata();
    uploadSignature.metadata.id = this.taskService.createNM();
    uploadSignature.metadata.fileName = this.taskService.createNM() + ".png";
    uploadSignature.metadata.rootId = "taskcentersignature";
    uploadSignature.metadata.docType = "png";
    const load = this.loadingService.show();
    this.taskService.uploadSignature(uploadSignature).subscribe(signId => {
      load.close();
      this.originSignId = signId;
      this.signature.emit(signId);
      this.signatureImage = image;
      this.signDialog.close();
    }, error => {
      load.close();
      this.messageService.error('ä¸ä¼ ç­¾åå¤±è´¥');
    });
  }



  /**
   * è·åç¨æ·çæè¿ä¸æ¬¡ç­¾å
   */
  private getUserSign(userId: string) {
    const load = this.loadingService.show();
    this.taskService.getUserSign(userId).subscribe(result => {
      load.close();
      if (result && result.length) {
        var sign;
        if (result.length == 1) {
          sign = result[0];
        } else {
          const list = result.filter(a => a && a.lastModifiedTime);
          sign = list.reduce(function (a, b) {
            return a.lastModifiedTime > b.lastModifiedTime ? a : b;
          })
        }
        this.signature.emit(sign.id);
        this.signatureImage = this.downloadService.getDownloadUrl(sign.id, 'taskcentersignature');
        this.tmpSignatureImage = this.signatureImage;
      }
    }, error => {
      console.error(error);
      load.close();
    })
  }

  /**
   * æ°å¢ä¸æ¡å¸¸ç¨æè§
   */
  add() {
    const newEntity = CommonComment.newEntity();
    newEntity.sortOrder = this.commentList.length + 1;
    this.commentList = this.commentList.concat(newEntity);
  }

  /**
   * å é¤ä¸æ¡æè§ data.rowData
   */
  clickToDeleteComment(data: any) {
    if (data.rowData.userId !== '*') {
      this.commentList = this.commentList.filter(c => c.id != data.rowData.id)
      this.dataGrid.loadData(this.commentList);
    }
  }

  /**
   * å¸¸ç¨æè§ä¿å­
   */
  save() {
    this.dataGrid.endCellEdit();
    this.httpSave().subscribe(r => {
      this.loadComments();
      this.commentDialog.close();
    });
  }

  /**
   * è°ç¨æ¥å£æ§è¡æ°å¢å é¤ãä¿®æ¹
   */
  private httpSave(): Observable<any> {
    const url = `/api/runtime/wf/v1.0/commonComments`;
    const originCommentIds = this.originCommentList.filter(o => o.userId !== '*').map(d => d.id);
    //å é¤ç
    const deleteIds = originCommentIds.filter(id => this.commentList.findIndex(c => c.id === id) < 0);

    return forkJoin(deleteIds.map(id => this.httpSvc.delete(url + `/${id}`, null))
      .concat(this.commentList.map(add => this.httpSvc.post(url, add))));
  }

  /**
 * webç«¯æå¼éä»¶é¢è§
 */
  attachmentPreview(metadataId) {
    const options = {
      appType: "app",
      funcId: "",
      tabName: "ææ¡£å¨çº¿é¢è§",
      appId: "6ccb476b-f2ba-df8f-6f47-d37d100c56e2",
      appEntrance: "docviewertab",
      tabId: new Date().getTime(),
      entityParams: {
        metadataIdList: [metadataId],
        rootId: "wf_attachment",
        metadataId: null,
        viewOptions: {
          showHeader: true,
          showPaging: true,
          showThumbnails: true,
          showDownload: true,
          showSearch: true,
          showZoom: true,
          showPrint: true,
          showSecondaryToolBar: true,
        },
      },
    };
    (window as any).gspframeworkService.rtf.func.openMenu(options);
  }

  /**
   * ç³»ç»é¢ç½®çä¸åè®¸ç¼è¾å é¤
   * @param data data.rowDataä¸ºå®ä½æ°æ®
   * @returns
   */
  beforeEdit = (data: any) => {
    if (data.rowData.userId == '*') {
      return of(false);
    }
    return of(true);
  }
}
