import { SysFarrisLookupComponent } from '@gsp-sys/sysmgr-ui';
import { NotifyService } from '@farris/ui-notify';
import { User } from '../../entity/user';
import { Assignee } from '../../entity/start-process-payload';
import { TaskService } from '../../service/task.service';
import { WfContext } from '../../entity/wfContext';

declare let System: any;

@Component({
  selector: 'lib-assignee-info',
  templateUrl: './assignee-info.component.html',
  styleUrls: ['../task-execute/task-execute.component.css'],
})
export class AssigneeInfoComponent implements OnInit, AfterViewInit {

  bindingData: { id?: string, name?: string } = {};//ç³»ç»å¸®å©å¤éæ¶ç¨å°
  myHelpBindingData: { id?: string, name?: string } = {};//èªå·±çå¸®å©å¤éæ¶ç¨å°
  assignUserNgModule: string;
  _assigneeInfo: AssigneeInfo;
  _selectedAssignees: User[];//å·²ç»éä¸­çææ´¾äºº
  assignees: Assignee[] = [];//è¿åéä¸­äººçIDæ°ç»
  searchAssignUserList = [];//éå¨åææ´¾çç¨æ·æ°æ®ï¼æç´¢ç¨
  anonymous = false; // æ¯å¦å¿å
  anonymousText = ''; // å¿åæ¾ç¤ºææ¬

  @Input() disabled: boolean = false;//åçäººåè¾å¥æ¡æ¯å¦ç¦ç¨
  @Input() placeholder: string;
  @Input() singleSelect: boolean;
  @Input() filter = { notGetCurrentUser: false, notGetStopUser: true, orderField: "order", withAllOrg: true, orgId: '' }
  @Input() ifShowBlank: boolean = true;//åçäººä¸ºç©ºæ¶ï¼æ¯å¦æ¾ç¤ºéäººæ¡
  @Input() openFromIframe: boolean = false;//æ¯å¦ä»iframeæå¼ï¼åºç¨IDPæäº¤å®¡æ¹éæ
  @Input() activityName: string;//èç¹åç§°
  @Input() allowGlobalAssign: boolean;//æ¯å¦å¨åææ´¾
  @Input() wfContext: WfContext;//å·¥ä½æµä¸ä¸æ

  @ViewChild('allUserDialog') myUserHelp: DialogComponent;
  @ViewChild('taskExecuteButtons') buttons: TemplateRef<any>;//ç¡®è®¤åæ¶æé®
  @ViewChild('allUserTable') myUserHelpTable: DataTableComponent;
  @ViewChild('allUserTable', { read: ElementRef }) dataTableEle: ElementRef;//ä¸ºdatatableå¢å é«åº¦

  @ViewChild(SysFarrisLookupComponent) sysUserHelp: SysFarrisLookupComponent;//ç³»ç»ç¨æ·å¸®å©
  @ViewChild('sysUserHelp', { read: ElementRef }) sysUserHelpEle: ElementRef; //ç³»ç»ç¨æ·å¸®å©DOMåç´ 
  @ViewChild('allUserDialog', { read: ElementRef }) myUserHelpEle: ElementRef;//èªå·±çå¸®å©DOMåç´ 

  @Output() afterConfirm = new EventEmitter<Assignee[]>();
  groupIcon = '<i class="f-icon f-icon-plus-circle" style="color: #2B87FF"></i>';
  extendUserHelp: boolean = false;//ç¨æ·å¸®å©æ¯å¦æ©å±
  extendUserHelpUrl: string;//æ©å±å¸®å©çurl

  columns = [
    { title: this.i18nService.getValue('static.columnName.code'), field: 'Code', width: 80 },
    { title: this.i18nService.getValue('static.columnName.name'), field: 'Name', width: 90 },
    { title: this.i18nService.getValue('static.columnName.department'), field: 'OrgName', width: 180 }
  ];

  originAssignUserList: {//éå¨åææ´¾çç¨æ·æ°æ®
    ID: string,
    Code: string,
    Name: string,
    OrgId: string,
    OrgName: string
  }[];

  constructor(
    private i18nService: I18nService,
    private notify: NotifyService,
    private cd: ChangeDetectorRef,
    private taskService: TaskService,
    private injector: Injector
  ) {
    //æ¯å¦ä½¿ç¨farrisç¨æ·å¸®å©
    this.taskService.getWfUserHelpConfig().subscribe((re: any) => {
      if (re && re.userHelpConfig && re.userHelpConfig.isExtend == 'true') {
        this.extendUserHelp = true;
        this.extendUserHelpUrl = re.userHelpConfig.url + '?v=' + new Date().getTime().toString();
      }
    }, error => {
      console.error(error);
    })
  }

  ngAfterViewInit(): void {
    // ä¸ºdata tableå¢å é«åº¦
    let ele = this.dataTableEle.nativeElement as HTMLElement;
    let body = ele && ele.querySelector('div.farris-datatable-container > div > div.farris-table-scorllable-body') as HTMLElement;
    body && (body.style.height = '290px');
  }

  ngOnInit() {
  }

  @Input()
  set assigneeInfo(info: AssigneeInfo) {
    this.disabled = false;
    this.placeholder = null;//æ¸ç©º
    this.assignUserNgModule = null;//æ¸ç©º
    this._assigneeInfo = info;//å¯ä»¥ä¼ null
    if (!this._assigneeInfo) {
      this.disabled = true;
    } else {
      if (this._assigneeInfo.selectAssignee) {
        this.disabled = false;
        this.placeholder = this.i18nService.getValue('task.selectPlaceholder');//è¯·éæ©
      } else {
        this.disabled = true;
        if (this._assigneeInfo.assigneeUsers && this._assigneeInfo.assigneeUsers.length) {
          this.placeholder = this.anonymous ? this.anonymousText : this._assigneeInfo.assigneeUsers.map(user => user.name).join();//è§£æäººå
        } else {
          this.placeholder = this.i18nService.getValue('task.noUser');//æ 
        }
      }
      this.singleSelect = !this._assigneeInfo.multiSelect;
      this.allowGlobalAssign = this._assigneeInfo.allowGlobalAssign;
      if (this._assigneeInfo.organizationScope && this._assigneeInfo.organizationScope.length) {
        const orgList = this._assigneeInfo.organizationScope[0];
        this.filter.orgId = orgList.id;
        this.filter.withAllOrg = orgList.includeChildren;
      }
    }
  }

  @Input()
  set setAnonymous(anonymous: boolean) {
    this.anonymous = anonymous;
  }

  @Input()
  set setAnonymousText(anonymousText: string) {
    this.anonymousText = anonymousText;
  }

  /**
   * å·²éä¸­çäººï¼å¸®å©ä¸å¯ç¨ï¼
   */
  @Input()
  set selectedAssignees(selectedAssignees: User[]) {
    !this._assigneeInfo && (this.placeholder = null);
    this._selectedAssignees = selectedAssignees;
    selectedAssignees.length && (this.placeholder = this.anonymous ? this.anonymousText : selectedAssignees.map(user => user.name).join());
  }

  /**
   * éæ©åä¸è
   */
  clickAssignUser() {
    if (this.extendUserHelp && !this.openFromIframe) {
      this.showExtendUserHelp();
    } else {
      this.showSysUserHelp();
    }
  }


  /**
   * å è½½æ©å±å¸®å©
   */
  private showExtendUserHelp() {
    System.import(this.extendUserHelpUrl).then((moduleFactories: any) => {
      const moduleFactory: any = Object.values(moduleFactories)[0];
      const moduleRef = moduleFactory.create(this.injector);
      let context = {
        singleSelect: this.singleSelect,
        allowGlobalAssign: this.allowGlobalAssign,
        assigneeUsers: this._assigneeInfo && this._assigneeInfo.assigneeUsers && this._assigneeInfo.assigneeUsers.length ? this._assigneeInfo.assigneeUsers : [],
        filter: this.filter,
        selectedUsers: this.assignees,
        wfContext: this.wfContext
      }
      moduleRef.instance.showHelp(context).subscribe(re => {
        let userList = re.userList;
        if (userList && userList.length) {
          this.assignees = userList.map(user => { return { id: user.id, name: user.name } });
          this.assignUserNgModule = this.assignees.map(user => user.name).join();
        } else {
          this.assignees = [];
          this.assignUserNgModule = '';
        }
        this.afterConfirm.emit(this.assignees);
        this.cd.detectChanges();
      }, error => {
        console.error(error);
      });
    }, error => {
      console.error(error);
    });
  }

  showSysUserHelp() {
    if (this._assigneeInfo && this._assigneeInfo.assigneeUsers && this._assigneeInfo.assigneeUsers.length) {
      this.getAssignUsersByIds();
      this.myUserHelp.show();
      setTimeout(() => {
        this.cd.detectChanges();//éæ°é¢è§£æå
        this.openFromIframe && this.openMyUserHelpOutIframe();
      }, 100);
    } else if (this.allowGlobalAssign) {
      //éä¸­çäººä¿æéä¸­
      this.bindingData = { id: this.assignees.map(a => a.id).join(), name: this.assignees.map(a => a.name).join() };
      this.sysUserHelp.showHelp();
      setTimeout(() => {
        this.cd.detectChanges();//éæ°é¢è§£æå
        this.openFromIframe && this.beforeOpenSysUserHelpOutIframe();
      }, 100);
    }

  }

  //èªå·±çç¨æ·å¸®å©å¨iframeå¤æå¼
  private openMyUserHelpOutIframe() {
    var myUserHelpDialog = this.myUserHelpEle.nativeElement as HTMLElement;
    top.document.body.append(myUserHelpDialog);//å¼¹å°é¡¶çº§windowä¸ï¼ä¸ç¨å è½½farrisæ ·å¼

    //æå¸®å©çé»è²é®ç½©å»æ
    var backdrop = window.document.querySelector("body > bs-modal-backdrop");
    backdrop.remove();

    var secondDiv = (myUserHelpDialog.firstChild as HTMLElement).firstChild as HTMLElement;//farris-dialogä¸çç¬¬äºçº§div
    secondDiv.style.width = '850px';
    secondDiv.style.height = '520px';
    secondDiv.style.marginLeft = '-425px';
    secondDiv.style.marginTop = '-260px';
  }

  private beforeOpenSysUserHelpOutIframe() {
    this.sysUserHelp.farrisLookUp.dialog.reSize({ width: 850, height: 520 });
    const sysUserHelpDialog = this.sysUserHelp.farrisLookUp.dialog.el.nativeElement as HTMLElement;
    sysUserHelpDialog.parentElement.removeChild(sysUserHelpDialog);
    this.sysUserHelp.farrisLookUp.draggable = false;
    this.sysUserHelp.farrisLookUp.resizable = false;
    this.sysUserHelp.farrisLookUp.showMaxButton = false;
    this.sysUserHelp.farrisLookUp.dialogOpened.subscribe(() => {
      setTimeout(() => {//å¦åä¸é¢çquerySelectorä¼æ¾ä¸å°
        this.openSysUserHelpOutIframe();
      }, 200)
    });
  }

  //ç³»ç»ç¨æ·å¸®å©å¨iframeå¤æå¼
  private openSysUserHelpOutIframe() {
    var sysUserHelpDialog = this.sysUserHelp.farrisLookUp.dialog.el.nativeElement as HTMLElement;
    this.sysUserHelp.farrisLookUp.dialog.reSize({ width: 850, height: 520 });
    top.document.body.append(sysUserHelpDialog);
  }

  //ç³»ç»ç¨æ·å¸®å©éäºº
  afterUsersSelect(data: any) {
    if (data) {
      data = this.singleSelect ? [data] : data;
      this.assignees = data.map(user => { return { id: user.id, name: user.name } });
      this.assignUserNgModule = data.map(user => user.name).join();
      this.afterConfirm.emit(this.assignees);
      this.cd.detectChanges();
    }
  }

  //æ ¹æ®assigneeInfo.assigneeIdsè·åææ´¾äººcodeãnameç­ä¿¡æ¯
  getAssignUsersByIds() {
    this.originAssignUserList = this._assigneeInfo.assigneeUsers.map(user => {
      return {
        ID: user.id,
        Name: user.name,
        Code: user.code,
        OrgId: user.orgId,
        OrgName: user.orgNamePath
      };
    }),
      this.searchAssignUserList = this.originAssignUserList;
  }

  //éå¨åææ´¾æç´¢
  myUserHelpSearch(event) {
    if (event.value) {
      const value = event.value;
      this.searchAssignUserList = this.originAssignUserList.filter(user =>
        (user.Name) && user.Name.includes(value) ||
        (user.Code) && user.Code.includes(value)
      );
    } else {
      this.getAssignUsersByIds();
    }
  }

  //éå¨åææ´¾éäºº
  myUserHelpAfterSelect() {
    if (this.myUserHelpTable.selections === undefined) {
      this.notify.warning(this.i18nService.getValue('static.assignee.noAssignee'));
      return;
    }
    const selectedUsers = this.singleSelect ? [this.myUserHelpTable.selections] : this.myUserHelpTable.selections;
    this.assignUserNgModule = selectedUsers.map(user => user.Name).join();
    this.assignees = selectedUsers.map(user => { return { id: user.ID, name: user.Name } });
    this.afterConfirm.emit(this.assignees);
    this.cd.detectChanges();
    this.myUserHelp.close();
  }

  //éå¨åææ´¾ç¹å»åæ¶åäºä»¶
  myUserHelpCancel() {
    this.myUserHelp.close();
  }

  //æ¸é¤
  assignUserClear() {
    this.assignees = [];
    this.assignUserNgModule = null;
    this.afterConfirm.emit([]);
    this.bindingData = {};
  }

}
